import React from 'react'
import styled from 'styled-components'

const Input = styled.input`
  display: grid;
  grid-row: 1;
  grid-column: 1;
  position: relative;
  width: 25px;
`

const hourOptions = [
  { value: '1', label: '1' },
  { value: '2', label: '2' },
  { value: '3', label: '3' },
  { value: '4', label: '4' },
  { value: '5', label: '5' },
  { value: '6', label: '6' },
  { value: '7', label: '7' },
  { value: '8', label: '8' },
  { value: '9', label: '9' },
  { value: '10', label: '10' },
  { value: '11', label: '11' },
  { value: '12', label: '12' },
]

const Timer = props => {
  return hourOptions.map(hour => {
    return (
      <Input
        key={hour.value}
        readOnly
        name="time"
        onClick={props.saveTime}
        value={hour.value}
      />
    )
  })
}
export default Timer
